import styles from '../pages/home/Home.module.css';

import { useFirestore } from '../hooks/useFirestore';

export default function TranscationList({ transcations }) {
 const { deleteDocument, response } = useFirestore('transcation');
 console.log(response);

 return (
  <ul className={styles.transactions}>
   {transcations.map((transcation) => (
    <li key={transcation.id}>
     <p className={styles.name}>{transcation.name}</p>
     <p className={styles.amount}>${transcation.amount}</p>
     <button onClick={() => deleteDocument(transcation.id)}>x</button>
    </li>
   ))}
  </ul>
 );
}
