import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import { useAuthContexct } from './hooks/useAuthContext';

//pages & components
import Home from './pages/home/Home';
import Login from './pages/login/Login';
import Signup from './pages/signup/Signup';
import Navbar from './components/Navbar';

function App() {
 const { authIsReady, user } = useAuthContexct();

 return (
  <div className='App'>
   {authIsReady && (
    <BrowserRouter>
     <Navbar />

     <Switch>
      <Route exact path='/'>
       {user && <Home />}
       {!user && <Redirect to='/login'></Redirect>}
      </Route>

      <Route path='/login'>
       {user && <Redirect to='/'></Redirect>}
       {!user && <Login />}
      </Route>

      <Route path='/signup'>
       {user && <Redirect to='/'></Redirect>}
       {!user && <Signup />}
      </Route>
     </Switch>
    </BrowserRouter>
   )}
  </div>
 );
}

export default App;
