import { useEffect, useState } from 'react';

import { useFirestore } from '../../hooks/useFirestore';

export default function TranscationForm({ uid }) {
 const [name, setName] = useState('');
 const [amount, setAmount] = useState('');

 const { addDocument, response } = useFirestore('transcation');

 const handleSubmit = (e) => {
  e.preventDefault();
  addDocument({ uid: uid, name: name, amount: amount });
 };

 useEffect(() => {
  if (response.success) {
   setAmount('');
   setName('');
  }
 }, [response.success]);

 return (
  <>
   <h3>Add Transcation :</h3>
   <form onSubmit={handleSubmit}>
    <label>
     <span>Transcation name : </span>
     <input
      required
      type='text'
      onChange={(e) => setName(e.target.value)}
      value={name}
     />
    </label>

    <label>
     <span>Amount : </span>
     <input
      required
      type='number'
      onChange={(e) => setAmount(e.target.value)}
      value={amount}
     />
    </label>
    <button>Add Transcation</button>
   </form>
  </>
 );
}
