import { useReducer, useEffect, useState } from 'react';
import { projectfirestore, timeStamp } from '../firebase/config';

let initalState = {
 document: null,
 isPending: false,
 error: null,
 success: null,
};

const firestoreReducer = (state, action) => {
 switch (action.type) {
  case 'IS_PENDING':
   return {
    isPending: true,
    success: false,
    document: null,
    error: null,
   };
  case 'DELETED_DOCUEMTN':
   return {
    isPending: false,
    document: null,
    success: true,
    error: null,
   };
  case 'ADD_DOCUEMTN':
   return {
    isPending: false,
    document: action.payload,
    success: true,
    error: null,
   };
  case 'ERROR':
   return {
    isPending: false,
    document: null,
    success: false,
    error: action.payload,
   };

  default:
   return state;
 }
};

export const useFirestore = (collection) => {
 const [response, dispatch] = useReducer(firestoreReducer, initalState);
 const [isCancelled, setIscancelled] = useState(false);

 // collection ref
 const ref = projectfirestore.collection(collection);
 const dispatchIfnotCancelled = (action) => {
  if (!isCancelled) {
   dispatch(action);
  }
 };
 // add documents
 const addDocument = async (doc) => {
  dispatch({ type: 'IS_PENDING' });

  try {
   const createdAt = timeStamp.fromDate(new Date());
   const addedDocument = await ref.add({ ...doc, createdAt: createdAt });

   dispatchIfnotCancelled({ type: 'ADD_DOCUMENT', payload: addedDocument });
  } catch (err) {
   dispatchIfnotCancelled({ type: 'ERROR', payload: err.message });
  }
 };

 // delete a document
 const deleteDocument = async (id) => {
  dispatch({ type: 'IS_PENDING' });

  try {
   await ref.doc(id).delete();

   dispatchIfnotCancelled({
    type: 'DELETED_DOCUEMTN',
    payload: deleteDocument,
   });
  } catch (err) {
   dispatchIfnotCancelled({ type: 'ERROR' });
  }
 };

 useEffect(() => {
  return () => {
   setIscancelled(true);
  };
 }, []);

 return { addDocument, deleteDocument, response };
};
